body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #000;
  height: 48px;
  padding: 4px;
  color: white;
  top: 0;
}

.App-title {
  font-size: 32px;
  font-weight: bold;
}

@media print {
  .App-header {
    display: none;
  }
}

.HomePage-blue {
  color: blue;
}

.HomePage-red {
  color: red;
}

table {
    text-align: left;
}

.right {
    text-align: right;
}

.HomePage-description {
    margin: 10px;
    overflow: hidden;
}
.AddConnection-input {
  font-size: medium;
}

td.center, th.center {
  text-align: center;
}
table {
  margin: auto;
  min-width: 20%;
}
table.blueTable {
  border: 1px solidblack;
  background-color: #EEEEEE;
  text-align: left;
  border-collapse: collapse;
}
table.blueTable td, table.blueTable th {
  border: 1px solid #AAAAAA;
  padding: 3px 2px;
}
table.blueTable tbody td {
  font-size: 13px;
}
table.blueTable tr:nth-child(even) {
  background: #D0E4F5;
}
table.blueTable thead {
  background:black;
  background: linear-gradient(to bottom, black 0%, black 66%,black 100%);
  border-bottom: 2px solid #444444;
}
table.blueTable thead th {
  font-size: 15px;
  font-weight: bold;
  color: #FFFFFF;
  border-left: 2px solid #D0E4F5;
}
table.blueTable thead th:first-child {
  border-left: none;
}

table.blueTable tfoot {
  font-size: 14px;
  font-weight: bold;
  color: #FFFFFF;
  background: #D0E4F5;
  background: linear-gradient(to bottom, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
  border-top: 2px solid #444444;
}
table.blueTable tfoot td {
  font-size: 14px;
}
table.blueTable tfoot .links {
  text-align: right;
}
table.blueTable tfoot .links a{
  display: inline-block;
  background:black;
  color: #FFFFFF;
  padding: 2px 8px;
  border-radius: 5px;
}

table.minimalistBlack {
  border: 3px solid #000000;
  text-align: left;
  border-collapse: collapse;
}
table.minimalistBlack td, table.minimalistBlack th {
  border: 1px solid #000000;
  padding: 5px 4px;
}
table.minimalistBlack tbody td {
  font-size: 13px;
}
table.minimalistBlack thead {
  background: #CFCFCF;
  background: linear-gradient(to bottom, #dbdbdb 0%, #d3d3d3 66%, #CFCFCF 100%);
  border-bottom: 3px solid #000000;
}
table.minimalistBlack thead th {
  font-size: 15px;
  font-weight: bold;
  color: #000000;
  text-align: left;
}
table.minimalistBlack tfoot {
  font-size: 14px;
  font-weight: bold;
  color: #000000;
  border-top: 3px solid #000000;
}
table.minimalistBlack tfoot td {
  font-size: 14px;
}


.HomePage-blue {
  color: blue;
}

.HomePage-red {
  color: red;
}

table {
    text-align: left;
}

.right {
    text-align: right;
}

.HomePage-description {
    margin: 10px;
    overflow: hidden;
}
.Commands-select {
  margin: auto;
  max-width: 280px;
}

.Commands-input {
  font-size: medium;
}

.TimeMachinePage-body {
  margin: 10px;
  text-align: left;
  font-family: Cantarell;
  font-size: large;
}

.TimeMachineControls-slider {
  margin-left: 10px;
  margin-right: 10px;
}

@media print {
  .Menu {
    display: none;
  }
  hr {
    display: none;    
  }
}

.Menu {
  margin: 0px;
  color: blue;
  background-color: white;
  font-size: 16px;
  font-weight: bold;
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  top: 0;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

hr {
    margin: 1px;
}

.Menu-selected {
  color: blue;
  background-color: lightGrey;
}

.Menu-red {
  color: red;
}

